<template>
  <div v-loading="loading">
    <TheHeader :ins="2" titleName="网页"></TheHeader>
    <div class="main-center">
      <div class="task-card-main">
        <div class="task-cont-box">{{ dataContet }}</div>
        <div class="task-info-box">
          <div class="task-form-box">
            <div class="task-form-main">
              <div class="task-from-row">
                <div class="task-from-name">标题</div>
                <el-input
                  v-model="dataFrom.title"
                  placeholder="请输入内容"
                ></el-input>
                <!-- <div class="task-from-right">T&nbsp;&nbsp;&nbsp;</div> -->
              </div>
              <div class="task-from-row">
                <div class="task-from-name">英文标题</div>
                <el-input
                  v-model="dataFrom.enTitle"
                  placeholder="请输入内容"
                ></el-input>
                <!-- <div class="task-from-right">enT</div> -->
              </div>
              <div class="task-from-row">
                <div class="task-from-name">作者</div>
                <el-input
                  v-model="dataFrom.auter"
                  placeholder="请输入内容"
                ></el-input>
                <!-- <div class="task-from-right">au</div> -->
              </div>
              <div class="task-from-row">
                <div class="task-from-name">作者英</div>
                <el-input
                  v-model="dataFrom.enAuter"
                  placeholder="请输入内容"
                ></el-input>
                <!-- <div class="task-from-right">EnAu</div> -->
              </div>
              <div class="task-from-row">
                <div class="task-from-name">年份</div>
                <el-input
                  v-model="dataFrom.year"
                  placeholder="请输入内容"
                ></el-input>
                <!-- <div class="task-from-right">year</div> -->
              </div>
              <div class="task-from-row">
                <div class="task-from-name">http</div>
                <el-input
                  v-model="dataFrom.http"
                  placeholder="请输入内容"
                ></el-input>
                <!-- <div class="task-from-right">PP</div> -->
              </div>
              <div class="task-from-row">
                <div class="task-from-name">时间</div>
                <el-input
                  v-model="dataFrom.date"
                  placeholder="请输入内容"
                ></el-input>
                <!-- <div class="task-from-right">EnPP</div> -->
              </div>
            </div>
            <div class="btn-box">
              <el-button type="primary" @click="verification">提交</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TheHeader from "./../../components/TheHeader";
import { journalshow, pagesubmit } from "./../../api/index";
export default {
  data() {
    return {
      loading: false,
      dataContet: "",
      roleCode: 0,
      taskCount: "",
      text: "",
      itemId: "",
      id: "",
      dataFrom: {
        title: "",
        enTitle: "",
        auter: "",
        enAuter: "",
        year: "",
        http: "",
        date: "",
      },
    };
  },
  components: {
    TheHeader,
  },
  created() {
    this.id = this.$route.query.id;
    this.itemId = this.$route.query.itemid;
    this.getDataList(this.itemId);
  },
  computed: {},
  mounted() {},
  methods: {
    getDataList(itemId) {
      this.loading = true;
      journalshow(itemId)
        .then((res) => {
          this.loading = false;
          if (res.code == 200) {
            this.dataContet = res.content;
          } else {
            this.notify(res.msg ? res.msg : "登录失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);
          this.loading = false;
        });
    },

    submit() {
      const username = JSON.parse(
        window.localStorage.getItem("userInfo")
      ).username;
      pagesubmit(
        {
          title: this.dataFrom.title,
          entitle: this.dataFrom.enTitle,
          author: this.dataFrom.enAuter,
          author_pinyin: this.dataFrom.auter,
          year: this.dataFrom.year,
          httplink: this.dataFrom.http,
          time: this.dataFrom.date,
        },
        this.id,
        this.itemId,
        username
      )
        .then((res) => {
          if (res.code == 200) {
            this.$confirm(`数据提交成功！`, "恭喜您", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              type: "success",
              center: true,
            })
              .then(() => {
                this.$router.push({ path: `/` });
              })
              .catch(() => {
                this.$router.push({ path: `/` });
              });
          } else {
            this.notify(res.msg ? res.msg : "处理失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);
          this.loading = false;
        });
    },
    verification() {
      if (
        this.dataFrom.title == "" ||
        this.dataFrom.title == undefined 
      ) {
        this.$confirm(`“标题”未填写，请填写后再提交！`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        });
        return false;
      } else if (
        this.dataFrom.enTitle == "" ||
        this.dataFrom.enTitle == undefined
      ) {
        this.$confirm(`“英文标题”未填写，请填写后再提交！`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        });
        return false;
      } else if (
        this.dataFrom.auter == "" ||
        this.dataFrom.auter == undefined
      ) {
        this.$confirm(`“作者”未填写，请填写后再提交！`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        });
        return false;
      } else if (
        this.dataFrom.enAuter == "" ||
        this.dataFrom.enAuter == undefined
      ) {
        this.$confirm(`“作者英”未填写，请填写后再提交！`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        });
        return false;
      } else if (this.dataFrom.year == "" || this.dataFrom.year == undefined) {
        this.$confirm(`“年份”未填写，请填写后再提交！`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        });
        return false;
      } else if (
        this.dataFrom.http == "" ||
        this.dataFrom.http == undefined
      ) {
        this.$confirm(`“http”未填写，请填写后再提交！`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        });
        return false;
      } else if (
        this.dataFrom.date == "" ||
        this.dataFrom.date == undefined
      ) {
        this.$confirm(`“时间”未填写，请填写后再提交！`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        });
        return false;
      } else {
        this.submit();
      }
    },
  },
};
</script>
